.App {
    text-align: center;
}

ul {
    padding-inline-start: 0;
}
* {
    margin-block-start: 0;
    margin-block-end: 0;
    margin-inline-start: 0;
    margin-inline-end: 0;
}
main {
    padding: 10px;
    width: 800px;
    max-width: 100%;
    margin: 0 auto;
    min-height: 500px;
    padding: 5px 3vw;
    color: rgb(238, 238, 238);
}

.no-line {
    text-decoration: none;
}

.members *,
.home *,
.contracts *,
.contract * {
    color: rgb(238, 238, 238);
}

/* forms */
form > * {
    font-size: 1.1em;
}
form label {
    padding-right: 10px;
    display: none;
}
form .input-field {
    background-color: #3d3f43;
    border: none;
    padding: 8px 16px;
    width: 250px;
    font-size: 1em;
}
form input:focus {
    outline: none;
    color: rgb(238, 238, 238);
}

form .submit-button {
    background-color: #1d5dab;
    border: none;
    padding: 8px 16px;
}

form .submit-button:hover {
    background-color: #2071d4;
    cursor: pointer;
}

