$primaryBackground: #131415;
$secondaryBackground: #2b2c30;
$primary: #3d3f43;
$secondary: #313336;
$standout: #1d5dab;
$standoutSelected: #2071d4;
$mainWhite: #eeeeee;

.group {
    background-color: $secondaryBackground;
    border: 1px solid $secondaryBackground;
}

.rule {
    background-color: $primary;
}

.group--header {
    background-color: $secondary;
    padding: 10px;
    border-radius: 5px;
}

.qb-lite .group--header:hover .group--header .group--drag-handler,
.qb-lite .group--header:hover .group--header .group--actions,
.qb-lite .group--header:not(:hover) .group--drag-handler,
.qb-lite .group--header:not(:hover) .group--actions,
.qb-lite .rule_group:not(:hover) .group--drag-handler,
.qb-lite .rule_group:not(:hover) .group--actions,
.qb-lite .rule:hover .rule .widget--valuesrc,
.qb-lite .rule:hover .rule .rule--drag-handler,
.qb-lite .rule:hover .rule .rule--header,
.qb-lite .rule:not(:hover) .widget--valuesrc,
.qb-lite .rule:not(:hover) .rule--drag-handler,
.qb-lite .rule:not(:hover) .rule--header {
    opacity: 1;
}

.qb-lite .rule:hover .rule .widget--valuesrc,
.qb-lite .rule:hover .rule .rule--drag-handler,
.qb-lite .rule:hover .rule .rule--header,
.qb-lite .rule:not(:hover) .widget--valuesrc,
.qb-lite .rule:not(:hover) .rule--drag-handler,
.qb-lite .rule:not(:hover) .rule--header {
    opacity: 1;
}
.group--children > .group-or-rule-container > .group-or-rule::before,
.group--children > .group-or-rule-container > .group-or-rule::after {
    border-color: $standout;
}

.query-builder-container label {
    padding: 6px 8px;
}

.rule--body select,
.rule--body input {
    background-color: $secondary;
    color: $mainWhite;
    padding: 4px 7px;
    margin: 3px;
}

.rule--header button {
    background-color: $secondary;
    border: none;
    padding: 6px;
    color: red;
}

.rule--header button:hover {
    cursor: pointer;
}

.group--actions button {
    background-color: $primary;
    color: $mainWhite;
    border: none;
    padding: 6px 10px;
    margin-right: 10px;
}

.group--actions button:nth-child(3) {
    background-color: $primary;
    padding: 6px;
    color: red;
}

.MuiInputBase-root,
.MuiInputBase-input {
    color: $mainWhite;
}

.query-builder-container .MuiSelect-select.MuiSelect-select {
    color: $mainWhite;
}

.query-builder-container .MuiButton-containedPrimary {
    background-color: $standout;
}

.query-builder-container .MuiSwitch-switchBase {
    color: $primaryBackground;
}

.query-builder-container .MuiSwitch-colorSecondary.Mui-checked {
    color: $standout;
}
