.login {
    background-color: #2b2c30;
    padding: 10px;
    width: 600px;
    max-width: 100%;
    margin: 0 auto;
    min-height: 300px;
    padding: 5px 3vw;
    color: rgb(238, 238, 238);
    padding-bottom: 50px;
}

.login * {
    color: rgb(238, 238, 238);
}
.header {
    padding: 15px 5px 25px 5px;
    border-bottom: 3px solid #1d5dab;
}
.login h2 {
    padding: 10px 5px 20px 5px;
}

.login form {
    padding-bottom: 20px;
    border-bottom: 2px solid #1d5dab;
}

.discord-login {
    background-color: #5865f2;
    color: white;
    margin: 20px;
    padding: 8px 10px;
    border: none;
    text-align: center;
}

.discord-login:hover {
    background-color: #5d68e6;
    cursor: pointer;
}

.discord-login div {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1px 5px;
}
.discord-login span {
    padding: 2px 10px;
    font-size: 1.1em;
}

@media only screen and (max-width: 675px) {
    .login {
        width: inherit;
    }
}

@media only screen and (max-width: 500px) {
    form .input-field {
        width: 200px;
    }
}

@media only screen and (max-width: 350px) {
    form .input-field {
        width: 150px;
        font-size: 0.9em;
    }

    .login h2 {
        font-size: 1em;
    }

    form > * {
        font-size: 0.9em;
    }

    .discord-login span {
        font-size: 0.9em;
    }
}
