.contract {
    padding: 10px;
    width: 1000px;
    max-width: 100%;
    margin: 0 auto;
    min-height: 300px;
    padding: 5px 3vw;
    color: rgb(238, 238, 238);
    padding-bottom: 50px;

    display: grid;
    grid-template-areas:
        "title title options"
        "main-page main-page options"
        "main-page main-page options"
        "main-page main-page admin"
        "main-page main-page .";
    grid-template-columns: 33% 33% auto;
    grid-gap: 25px;

    /* grid-template-columns: 35% auto 25% auto;
    grid-template-rows: 20px 20px 20px 20px; */
}

.contract > * {
    padding: 10px;
}

.contract h3 {
    padding: 5px;
}

.contract .title h1 {
    padding-bottom: 10px;
}

.contract .subtitle {
    display: flex;
    justify-content: center;
}

.contract .subtitle > p {
    padding: 5px;
}

.contract > * {
    background-color: #2b2c30;
}
.header {
    padding: 15px 5px 25px 5px;
    border-bottom: 3px solid #1d5dab;
}

.title {
    grid-area: title;
}

.main-page {
    grid-area: main-page;
}

/* Options */
.options {
    grid-area: options;
}

.options .revive-faction {
    display: flex;
    align-items: center;
    padding: 3px 1px;
}

.options .revive-faction > * {
    flex: 1;
    width: 100%;
    text-align: center;
    padding: 5px;
}

.options .revive-faction p {
    font-size: 0.9em;
}

.contract .button-container {
    padding: 5px;
    display: flex;
}

.contract .button-container > button {
    padding: 10px;
    flex: 1;
    border: none;
    border-radius: 0;
    text-align: center;
}

.contract .button-container > .not-selected {
    background-color: #3d3f43;
}
.contract .button-container > .not-selected:hover {
    cursor: pointer;
    background-color: #313336;
}

.contract .button-container > .selected {
    background-color: #1d5dab;
}

/* Admin panel */
.admin {
    grid-area: admin;
}

.admin-options {
    text-align: left;
    padding: 10px;
}

.admin-options a {
    padding: 15px;
    display: block;
}

.admin-options a:hover {
    cursor: pointer;
    background-color: #3d3f43;
}

.admin-options p {
    display: inline;
    padding-left: 10px;
}
/* 
Table */
.conditions .contract-table {
    margin: auto;
    padding: 10px;
    margin-bottom: 25px;
}

.conditions .contract-table tr > * {
    padding: 15px 5px;
}

.conditions .contract-table tr:nth-child(odd) > td {
    background-color: #313336;
}

.conditions .contract-table tr:nth-child(even) > td {
    background-color: #3d3f43;
}

.conditons .contract-table td {
    overflow: hidden;
}

/* Leaderboard specific */

.conditions .contract-table .td-name {
    width: 175px;
    max-width: 175px;
}
.conditions .contract-table .td-count {
    width: 60px;
    max-width: 60px;
}

.conditions .contract-table .td-skill {
    width: 60px;
    max-width: 60px;
}

.conditions .contract-table .td-faction {
    width: 200px;
}

/* Livefeed specific */

.conditions .contract-table .td-reviver {
    width: 110px;
}
.conditions .contract-table .td-target {
    width: 110px;
}

.conditions .contract-table .td-hosp-type {
    width: 105px;
}

.conditions .contract-table .td-timestamp {
    width: 150px;
}

.conditions .contract-table .td-success {
    width: 30px;
}

.conditions .contract-table .td-chance {
    width: 30px;
}

.contract-table td a {
    text-decoration: none;
}

/* Condition page */
.condition-page {
    padding: 15px 5px;
}
.line-ul {
    width: 100%;
}

.line-ul li {
    display: flex;
    width: 100%;
}
.line-ul li div {
    flex: 1;
    padding: 10px;
}

.line-ul li div:nth-child(odd) {
    text-align: right;
    padding-right: 50px;
}
.line-ul li div:nth-child(even) {
    border-left: 3px solid #1d5dab;
    text-align: left;
    padding-left: 50px;
}

@media only screen and (max-width: 1100px) {
    .contract {
        width: inherit;
    }
}

@media only screen and (max-width: 1000px) {
    .contract {
        padding: 5px 2vw;
    }
}

@media only screen and (max-width: 850px) {
    .conditions .contract-table tr > * {
        padding: 10px 3px;
        font-size: 0.9em;
    }
}

@media only screen and (max-width: 750px) {
    /* True mobile */
    .contract {
        padding: 10px;
        max-width: 100%;
        margin: 0 auto;
        min-height: 300px;
        padding: 5px 1vw;
        color: rgb(238, 238, 238);
        padding-bottom: 25px;
        display: grid;
        grid-template-areas:
            "title"
            "options"
            "main-page"
            "main-page"
            "admin";
        grid-template-columns: auto;
        grid-gap: 10px;
    }
}

@media only screen and (max-width: 500px) {
    .contract > * {
        padding: 5px;
    }
    .conditions .contract-table .td-reviver {
        width: 20%;
    }
    .conditions .contract-table .td-target {
        width: 20%;
    }

    .conditions .contract-table .td-hosp-type {
        width: 18%;
    }

    .conditions .contract-table .td-timestamp {
        width: 18%;
    }

    .conditions .contract-table .td-success {
        width: 3%;
    }

    .conditions .contract-table .td-chance {
        width: 3%;
    }

    .line-ul li div:nth-child(odd) {
        padding-right: 25px;
    }
    .line-ul li div:nth-child(even) {
        padding-left: 25px;
    }

    .line-ul li div {
        padding: 5px;
        font-size: 0.9em;
    }

    .conditions .contract-table {
        padding: 0;
    }

    .conditions .contract-table tr > * {
        font-size: 0.7em;
    }

    .conditions .contract-table {
        width: 95vw;
    }
}
