.new-contract-container {
    justify-content: flex-start;
}

.react-datepicker__time-container
    .react-datepicker__time
    .react-datepicker__time-box
    ul.react-datepicker__time-list
    li.react-datepicker__time-list-item,
.react-datepicker__day-name,
.react-datepicker__day,
.react-datepicker__time-name,
.react-datepicker__current-month,
.react-datepicker-time__header,
.react-datepicker-year-header,
.react-datepicker__current-month,
.react-datepicker-time__header,
.react-datepicker-year-header {
    color: #f0f0f0;
}

.react-datepicker__time-container {
    border: none;
}

.react-datepicker {
    border-radius: 0;
    padding: 8px;
    margin: 5px;
    background-color: #3d3f43;
    color: #f0f0f0;
    border: none;
}

input:focus {
    outline: 0;
}

.react-datepicker:hover {
    /* background-color: #1d5dab; */
    cursor: pointer;
}

.react-datepicker__time-container .react-datepicker__time,
.react-datepicker__header {
    background-color: #313336;
    border: none;
}

.react-datepicker__time-container
    .react-datepicker__time
    .react-datepicker__time-box
    ul.react-datepicker__time-list
    li.react-datepicker__time-list-item,
.react-datepicker__day-name,
.react-datepicker__day,
.react-datepicker__time-name,
.react-datepicker__current-month,
.react-datepicker-time__header,
.react-datepicker-year-header,
.react-datepicker__current-month,
.react-datepicker-time__header,
.react-datepicker-year-header {
    color: #f0f0f0;
}
.react-datepicker__time-container
    .react-datepicker__time
    .react-datepicker__time-box
    ul.react-datepicker__time-list
    li.react-datepicker__time-list-item {
    height: 30px;
    padding: 5px 10px;
    white-space: nowrap;
}
.home .date-container * {
    color: rgb(238, 238, 238);
}

.react-datepicker__year-read-view--down-arrow,
.react-datepicker__month-read-view--down-arrow,
.react-datepicker__month-year-read-view--down-arrow,
.react-datepicker__navigation-icon::before {
    top: 12px;
}

.react-datepicker__time-container
    .react-datepicker__time
    .react-datepicker__time-box
    ul.react-datepicker__time-list
    li.react-datepicker__time-list-item:hover {
    background-color: #3d3f43;
}

.react-datepicker__day:hover,
.react-datepicker__month-text:hover,
.react-datepicker__quarter-text:hover,
.react-datepicker__year-text:hover {
    background-color: #313336;
}

.react-datepicker__time-list::-webkit-scrollbar {
    background-color: #313336;
    width: 4px;
}

.react-datepicker__time-list::-webkit-scrollbar-thumb {
    background-color: #58595b;
    width: 4px;
}

.date-container {
    display: flex;
    width: 300px;
    justify-content: center;
    align-items: center;
    max-width: 300px;
    margin: 10px auto;
}

/* .date-container > * {
    flex: 3;
}*/

.new-contract .date-icon {
    display: inline-block;
    width: 100px;
}

.new-contract .input-container {
    margin: 10px;
}

.new-contract .input-container input {
    margin-left: 10px;
    background-color: #3d3f43;
    color: #f0f0f0;
    border: none;
    padding: 8px;
}

/* .new-contract input[type="radio"] {
    min-width: 100px;
    background-color: blue;
    color: green;
} */

.new-contract .line {
    position: absolute;
    display: inline-block;
    width: 2px;

    background-color: #1d5dab;
    height: 28px;
    margin-left: 5px;
    margin-top: 38px;
}

/* Checkbox */
.new-contract .icon svg {
    color: #1d5dab;
}

.new-contract .revive-factions {
    margin: auto;
    justify-content: center;
    width: 300px;
}
.new-contract .revive-factions > * {
    margin: 10px;
}
.revive-factions label span:nth-child(odd):hover {
    cursor: pointer;
}
