.members {
    background-color: #2b2c30;
    padding: 10px;
    width: 800px;
    max-width: 100%;
    margin: 0 auto;
    min-height: 500px;
    padding: 5px 3vw;
    color: rgb(238, 238, 238);
    padding-bottom: 50px;
}

.members * {
    color: rgb(238, 238, 238);
}
.members h3 {
    text-align: left;
    font-size: 1.5em;
    padding: 15px 5px;
    font-weight: normal;
}
.member-table-container {
    padding-top: 20px;
}
.member-table {
    border-spacing: 0;
    margin: auto;
    width: 600px;
}

.member-table th {
    padding: 0 15px;
    margin: 0;
    font-size: 1.1em;
    font-weight: 600;
}

.member-table .status {
    padding: 0 5px;
    margin: 0;
    font-size: 1.1em;
    font-weight: 600;
}

.member-table .status {
    padding-top: 20px;
    padding-bottom: 3px;
    width: 50px;
}

.member-table .body {
    padding: 10px 10px;
    font-size: 1.1em;
    overflow: hidden;
}
.member-table .line {
    padding: 0;
    margin: 0;
    min-width: 65px;
}

.member-table .line div {
    background-color: #1d5dab;
    width: 2px;
    margin: 0 auto;
    height: 55px;
    padding: 0;
}

.member-table tr:nth-child(odd) .body {
    background-color: #313336;
}

.member-table tr:nth-child(even) .body {
    background-color: #3d3f43;
}

.member-table tr .name {
    width: 26%;
}

.member-table tr .name a {
    text-decoration: none;
}

.member-table tr .skill {
    width: 15%;
}

.member-table tr .access-level {
    width: 26%;
}

.member-table tr .faction {
    width: 33%;
}

.members .list-factions {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.members .list-factions > * {
    width: 610px;
}

@media only screen and (max-width: 850px) {
    .members {
        width: inherit;
    }
}

@media only screen and (max-width: 650px) {
    .member-table {
        width: 100%;
    }
    .members .list-factions > * {
        width: 90%;
    }
    .members {
        padding: 5px;
    }

    /* LINE */
    .member-table .status {
        width: 40px;
        font-size: 0.7em;
        padding-left: 0;
    }
    .member-table .line {
        min-width: 40px;
    }
    .member-table .body {
        padding: 5px;
    }
}

@media only screen and (max-width: 575px) {
    .member-table th {
        padding: 0 5px;
        font-size: 1em;
    }
    .member-table .body {
        font-size: 1em;
    }
}

@media only screen and (max-width: 425px) {
    .member-table th {
        padding: 0;
        font-size: 0.8em;
    }
    .member-table .body {
        font-size: 0.8em;
    }
}
