* {
    margin-block-start: 0;
    margin-block-end: 0;
    margin-inline-start: 0;
    margin-inline-end: 0;
}
.home {
    background-color: #2b2c30;
    padding: 10px;
    width: 800px;
    max-width: 100%;
    margin: 0 auto;
    min-height: 500px;
    padding: 5px 3vw;
    color: rgb(238, 238, 238);
    padding-bottom: 50px;
}
.header {
    padding: 15px 5px 25px 5px;
    border-bottom: 3px solid #1d5dab;
}

.header h1 {
    margin-block: 0;
    font-size: 2.5em;
}
.home * {
    color: rgb(238, 238, 238);
}
.main-body {
    text-align: left;
}
.table-parent {
    padding-top: 15px;
    text-align: center;
}
.home-table {
    margin: 15px auto;
}
.home-table th,
.home-table td {
    padding: 15px 20px;
}

.home-table tr:nth-child(odd) {
    background-color: #313336;
}

.home-table tr:nth-child(even) {
    background-color: #3d3f43;
}

.table-parent .home-table thead tr {
    background-color: #3d3f43;
}

h4 {
    font-size: 1.5em;
}

.info {
    padding: 20px;
}

.info p {
    padding-top: 10px;
}

@media only screen and (max-width: 1000px) {
    header .home {
        padding: 0;
    }
}

@media only screen and (max-width: 550px) {
    .home .header {
        padding: 15px 0 25px 0;
    }

    .home .header h1 {
        font-size: 1.9em;
    }

    .home .header h3 {
        font-size: 1em;
    }
    .home-table th,
    .home-table td {
        padding: 10px 10px;
    }

    .home .home-table {
        font-size: 0.9em;
    }
    .table-parent p {
        font-size: 0.9em;
    }
}

@media only screen and (max-width: 450px) {
    .home-table th,
    .home-table td {
        padding: 5px 10px;
    }

    .home .home-table {
        font-size: 0.8em;
    }
    .table-parent p {
        font-size: 0.8em;
    }
}

@media only screen and (max-width: 400px) {
    .home-table th,
    .home-table td {
        padding: 6px;
    }
}
