.contracts {
    background-color: #2b2c30;
    padding: 10px;
    width: 800px;
    max-width: 100%;
    margin: 0 auto;
    min-height: 500px;
    padding: 5px 3vw;
    color: rgb(238, 238, 238);
    padding-bottom: 50px;
}

/* Conditions */

/* Admin conditons */
.admin-conditions {
    width: 400px;
    padding: 15px 0;
    margin: auto;
}

.contracts .add-contract-outer {
    display: flex;
    justify-content: left;
}
.contracts .add-contract {
    margin-top: 10px;
    padding: 10px;
    text-align: left;
    margin: 0;
    text-decoration: none;

    color: rgb(238, 238, 238);
}

.add-contract:hover {
    background-color: #3b3d41;
}
.contracts .add-contract > * {
    padding-right: 10px;
}
.contracts .conditions {
    display: flex;
}

.contracts .conditions > div,
.contracts .conditions > form {
    flex: 1;
    margin: 10px;
}

.contracts ul {
    margin-top: 20px;
}

.contracts ul li {
    text-decoration: none;
    margin: 10px 0;
    padding-top: 20px;
    border-top: 2px solid #1d5dab;
}

.contract-grid {
    display: grid;
    grid-template-areas:
        "title payment start button"
        "factions payment start button"
        "factions payment end status"
        "factions payment end status";
    grid-gap: 5px;

    grid-template-columns: 35% auto 25% auto;
    grid-template-rows: 20px 20px 20px 20px;
}

.contract-grid > * {
    display: flex;
    justify-content: center;
    align-items: center;
}

.contract-title {
    grid-area: title;

    min-width: 5px;
    min-height: 5px;
    text-align: center;
}

.contract-title h2 {
    font-size: 1.2em;
    text-align: center;
}

.contract-factions {
    grid-area: factions;
    padding-bottom: 5px;
    min-width: 5px;
    min-height: 5px;
}

.contract-payment {
    grid-area: payment;

    flex-direction: column;
}

.contract-start {
    grid-area: start;
    align-items: flex-end;
    min-width: 5px;
    min-height: 5px;
}

.contract-end {
    grid-area: end;
    align-items: flex-start;
    min-width: 5px;
    min-height: 5px;
}

.contract-button {
    grid-area: button;

    min-width: 5px;
    min-height: 5px;
    align-items: flex-end;
}

.contract-button .button-link {
    background-color: #1d5dab;
    border: none;
    text-decoration: none;
    padding: 10px;
}

.contract-button .button-link:hover {
    background-color: #2071d4;
}

.contract-status {
    grid-area: status;
    min-width: 5px;
    min-height: 5px;
    padding-top: 5px;
    align-items: flex-start;
}

.contract-status .ended {
    color: red;
}

.contract-status .live {
    color: green;
}

.contract-status .calculating {
    color: gray;
}

@media only screen and (max-width: 875px) {
    .contracts {
        width: inherit;
    }
    .contracts .conditions {
        display: block;
    }
    .contracts .conditions > div {
        width: 400px;
        margin: auto;
        margin-top: 20px;
    }
}

@media only screen and (max-width: 710px) {
    .contract-grid * {
        font-size: 0.9em;
    }
}

@media only screen and (max-width: 500px) {
    .contract-button .button-link {
        padding: 7px;
    }

    .contract-grid {
        grid-template-areas:
            "title title  button"
            "factions factions status"
            "payment start end";
        grid-gap: 3px;

        grid-template-columns: auto 30% 30%;
        grid-template-rows: inherit;
    }
    .contract-start,
    .contract-end {
        align-items: center;
    }
}

@media only screen and (max-width: 500px) {
    .admin-conditions {
        width: 90%;
    }
    .contracts .conditions > div {
        width: 90%;
    }

    .contracts ul li {
        padding-bottom: 10px;
    }

    form > * {
        font-size: 0.9em;
    }

    form .input-field {
        font-size: 0.9em;
    }

    form .input-field {
        width: 60%;
    }

    form .submit-button {
        width: 25%;
        padding: 8px;
    }
}
