/* Pagination */

.pagination {
    display: flex;
    margin-top: 10px;
    padding: 5px;
}
div .arrow-container {
    background-color: #3d3f43;
    border-radius: 50%;
    height: 40px;
    width: 40px;
    display: inline-block;
    border: none;
}

.pagination div .arrow-container:hover {
    background-color: #313336;
}

.arrow-container svg {
    padding-top: 3px;
}

.pagination > * {
    flex: 1;
}

.outer-pagination-nav {
    display: flex;
    flex: 7;
    padding-right: 10px;
}

.pagination-nav {
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 5;
}

.pagination-nav > * {
    flex: 1;

    text-align: center;
}
.show-rows {
    flex: 2;
}

.pagination .left > * {
    padding-right: 2px;
    padding-bottom: 3px;
}

.pagination .right > * {
    padding-left: 2px;
    padding-bottom: 3px;
}

.pagination-nav div p {
    display: block;
    font-size: 0.9em;
    padding: 5px 8px;
    border-radius: 2px;
}

/* .pagination *:hover {
    cursor: pointer;
} */

.pagination .arrow-container:hover {
    cursor: pointer;
}

.pagination .show-rows:hover {
    cursor: pointer;
}
.pagination .active-page {
    background-color: #1d5dab;
    /* padding: 5px 5px 7px 5px; */
}

.pagination .pagination-nav button {
    background-color: transparent;
    border: none;
    /* padding: 5px 10px; */
    display: block;

    text-align: center;
    margin: auto;
    min-width: 50px;
    padding: 8px 15px;
}

.pagination button:hover {
    background-color: #3d3f43;
    cursor: pointer;
}

@media only screen and (max-width: 1000px) {
    .show-rows {
        flex: 3;
    }
}

@media only screen and (max-width: 875px) {
    .pagination {
        display: block;
    }

    .show-rows {
        padding: 10px 10px 0 10px;
    }
}

@media only screen and (max-width: 500px) {
    .pagination .pagination-nav button {
        min-width: inherit;
    }

    .pagination .pagination-nav button {
        padding: 4px 8px;
    }

    .pagination-nav div p {
        padding: 3px 4px;
    }

    div .arrow-container {
        width: 30px;
        height: 30px;
    }

    .outer-pagination-nav {
        padding: 0;
    }

    .pagination .right > *,
    .pagination .left > * {
        padding: 0;
    }
}
