nav {
    display: flex;
    align-items: center;
    min-height: 100px;
    background-color: #2b2c30;
    color: white;
    margin-bottom: 25px;
}

.inner-nav {
    width: 1000px;
    max-width: 100%;
    display: flex;
    margin: auto;
    border: none;
}

.nav-links {
    display: none;
    align-items: center;
    list-style: none;
    text-decoration: none;
    margin: auto;
    padding: 0;
}

.nav-links * {
    list-style: none;
    text-decoration: none;
}

.nav-links > li {
    padding: 10px 15px;

    text-decoration: none;
}

.nav-link {
    color: white;
    align-items: center;
    display: flex;
    padding: 10px 25px;
}

.nav-link .icon {
    font-size: 22px;
}

.nav-text {
    padding: 2px;
    margin-left: 8px;
    font-size: 18px;
}

/* .selectedBox {
      background-color: #3D3F43;
  }


  .nav-link:hover .nav-text {
      border-bottom: 2px solid #1D5DAB;
  } */

.active span {
    border-bottom: 2px solid #1d5dab;
}

.nav-link:hover {
    background-color: #3d3f43;
}

img {
    width: 80px;
    border-radius: 25%;
}

.right {
    margin-left: 1;
}

@media only screen and (max-width: 1000px) {
    .inner-nav {
        width: 100%;
    }

    .inner-nav img {
        padding: 5px 10px;
    }

    .nav-links {
        align-items: center;
        margin: 0;
    }

    .nav-links li {
        padding: 10px;
    }

    .nav-links li div {
        padding: 10px 15px;
    }
}

/* Dropdown menu */
.dropdown-container {
    display: none;
    width: 100%;
    margin: 2px 10px;
}

.dropdown-container button {
    background-color: #2b2c30;
    border: none;
    color: white;
    padding: 10px;
    display: flex;
    align-items: center;
}

.dropdown-container button p {
    display: inline;
    font-size: 18px;
    padding-left: 10px;
    text-align: center;
}

/* .dropdown-container button svg {
    padding-left: 5px;
} */

.dropdown-container .navbar-icon {
    color: white;
}

.dropdown-container .navbar-icon:hover {
    cursor: pointer;
    color: #3d3f43;
}

@media only screen and (min-width: 850px) {
    .inner-nav .nav-links {
        display: flex;
    }
}
@media only screen and (max-width: 850px) {
    .dropdown-container {
        display: flex;
        align-items: center;
        justify-content: flex-end;
    }

    .inner-nav .nav-links {
        display: none;
    }
    .dropdown-container .button {
        font-size: 18px;
    }

    /* actual dropdown */
    .inner-nav .nav-links {
        position: absolute;
        height: 100%;
        max-height: 100%;
        width: 100%;
        background-color: #2b2c30;
        margin-top: 90px;
        overflow: hidden;
        overflow-y: hidden;
        z-index: 100;
    }

    /* .inner-nav .nav-links li {
        border-bottom: 1px solid white;
    } */
    .nav-link {
        background-color: #313336;
    }
    /* Toggle features */
    #disableScrolling {
        height: 100%;
        overflow: hidden;
    }
    .inner-nav #showDropDown {
        display: block;
    }
}

@media only screen and (max-width: 600px) {
    nav {
        min-height: 75px;
    }
    .inner-nav img {
        padding-left: 5px;
        width: 70px;
    }
    .inner-nav .nav-links {
        margin-top: 80px;
    }
}

